@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
*{
  margin:0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: #1a191d;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
  scroll-behavior: smooth;
}
.body{
    flex:1 1;
}

.heading{
    font-family: 'Inter';
    font-size: 21px;
    font-weight: 500;
    color: white;
    padding-top: 20px;
    padding-bottom: 5px;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    flex-wrap: wrap;
    flex: 1 1;
}

.contact{
    font-family: 'Inter';
    color: white;
    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
    height: 450px;
    background-color: #131212;
    display: flex;
    flex-wrap: wrap;
    flex: 1 1;
}


.product{
    margin-top: 20px;
    margin-left: 20px;
    margin-right: 20px;
    border-radius: 5px;
    display: grid;
}

.openBadge{
    background-color: #003cd3;
}

.mentr{
    background-color: purple;
}

.productDetails{
    padding-top:30px;
    padding-bottom:30px;
    padding-left:30px;
    padding-right:30px;
    font-family: 'Inter';
    line-height: 1.5rem;
    color: white;
}

.productName{
    margin-bottom: 15px;
}

.productDescription{
    font-weight: 380;
    opacity: 0.7;
    margin-bottom: 15px;
}

.getStarted{
    font-family: 'Inter';
    cursor: pointer;
    border-radius: 3px;
    background-color: #20292f;
    padding-bottom: 10px;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 10px;
    color: white;
    border:none;
}

.cards{
    padding-top: 30px;
    padding-bottom: 30px;
    padding-right: 10px;
    padding-left: 10px;
    margin: 0 auto;
    display: grid;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}

.features{
    border-radius: 3px;
    color: white;
    font-size: 15px;
    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 20px;
}

.icon{
    height:40px;
    width:40px;
    margin-bottom: 10px;
    background-image: url(/epoch-zero/static/media/antimony.d1ba35a3.svg);
    background-position: center;
    background-size: cover;
}

.featureName{
    letter-spacing: 2px;
    font-weight: 400;
    font-size: medium;
}

.fetaureDescription{
    font-family: 'Inter';
    font-weight: 380;
    line-height: 1.5rem;
    opacity: 0.7;
    margin-top: 10px;
}

.show{
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 30px;
    display: grid;
    grid-row-gap: 30px;
}

.showItem{
    padding-top:30px;
    padding-bottom:30px;
    border-radius: 5px;
    line-height: 1.5rem;
    color: white;
    background-color: #003cd3;
}

.projects{
    background-image: url(/epoch-zero/static/media/ProBG.1bb919cb.jpg);
    background-position: center;
    background-size: cover;
    
}

.showDetails{
    font-family: 'Inter';
    margin-left: 30px;
    margin-right: 30px;
}

.showName{
    font-size:25px;
    margin-bottom: 15px;
}

.showDescription{
    font-weight: 400;
    opacity: 0.7;
    margin-bottom: 25px;
}

.view{
    font-family: 'Inter';
    cursor: pointer;
    border-radius: 3px;
    background-color: #6f6f6f;
    padding-bottom: 10px;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 30px;
    background-color: #003cd3;
    color: white;
    border:none;
}

@media (min-width: 450px) {

.cards { grid-template-columns: repeat(2, 1fr);}
.show { grid-template-columns: repeat(1, 1fr);}
.product { grid-template-columns: repeat(1, 1fr);}
.showItem{
    border-radius: 5px;
}
}

@media (min-width: 800px) {

.cards { grid-template-columns: repeat(3, 1fr); }
.show { grid-template-columns: repeat(2, 1fr);}
.product { grid-template-columns: repeat(2, 1fr);}
.showItem{
    border-radius: 0;
}
}

@media (min-width: 1200px) {
.cards { grid-template-columns: repeat(4, 1fr); }
.heading{
    padding-left: 30px;
    padding-right: 30px;
}
.tagline{
    padding-left: 30px;
    padding-right: 30px;
}
.show{
    margin-left: 120px;
    margin-right: 120px;
    grid-row-gap: 0;
}
.showItem{
    border-radius: 0;
}
.showDetails{
    padding-top:30px;
    padding-bottom:30px;
    font-family: 'Inter';
    margin-left: 50px;
    margin-right: 50px;
}
.product{
    margin-top: 40px;
    margin-left: 120px;
    margin-right: 120px;
}
.productDetails{
    padding-top:60px;
    padding-bottom:60px;
    padding-left:60px;
    padding-right:140px;
}
.cards{
    padding-right: 120px;
    padding-left: 120px;
}
.features{
    background: none;
}
.features:hover{
    background-color: #262738;
}
}
.works{
    display: flex;
    flex: 1 1;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 25px;
}

.worksContainer{
    flex: 1 1;
    padding-top: 30px;
    padding-bottom: 30px;
    padding-right: 10px;
    padding-left: 10px;
    margin: 0 auto;
    display: grid;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}

.imageWork{
    width: 100%;
    box-shadow: 0px 0px 40px 0 rgb(0,0,0,0.25);
}

.spacing{
    display: none;
    flex:0.15 1;
}


@media (min-width: 450px) {

    .worksContainer { grid-template-columns: repeat(2, 1fr);}
    }
    
    @media (min-width: 800px) {
    .spacing{display: flex;}
    .worksContainer {flex:0.7 1; grid-template-columns: repeat(3, 1fr); }
    }

.header{
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 20px;
    padding-left: 20px;
    position: -webkit-sticky;
    position: sticky;
    z-index: 200;
    top:0;
    background-color: #1a191d;
}


.nav{
    flex:1 1;
    display: flex;
    color:white;
    font-family: 'Inter';
    font-weight: 900;
    font-size: 20px;
}

.link{
    color: white;
    font-size: 15px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 3px;
}

.logo{
    height: 60px;
    width: 60px;
    border-radius: 100px;
    background-position: center;
    background-size: cover;
    background-color: white;
    box-shadow: 5px 5px 30px 0 rgba(0, 0, 0, 0.2);
}

.break{
    display: block;
}

@media(min-width:800px){
    .break{display: none;}
}
.footer{
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #003cd3;
    color: white;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
    z-index: 500;
}

@media(min-width:800px){
    .footer{position: fixed; background-color: #313131;}
}
.workDetails{
    background-color: #6823ff;
    display: flex;
    font-family: Inter;
    flex: 1 1;
    line-height: 1.8rem;
    color:white;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 35px;
    padding-bottom: 25px;
}

.workContainer > h1{
    margin-bottom: 5px;
    font-weight: 800;
}

.workContainer > hr{
    opacity: 0.35;
    background: none;
    border-color: white;
    height:1px;
}

.workContainer > h3{
    font-weight: 800;
    font-size: 22px;
    margin-bottom: 40px;
}

.workContainer > p{
    font-size: 20px;
    font-weight: 700;
}

.imageContainer{
    padding-top: 30px;
    padding-bottom: 10px;
    margin: 0 auto;
    display: grid;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}

.displayImage{
    color: white;
    font-size: 15px;
}

.appImage:hover{
    z-index:200;
    box-shadow: 0px 0px 40px 0 rgb(0,0,0,0.25);
}
    @media (min-width: 800px) {
    
    .imageContainer { grid-template-columns: repeat(2, 1fr); }
    
    }
    
    @media (min-width: 1200px) {
    .imageContainer { grid-template-columns: repeat(2, 1fr); }
    
    }

.app__page{
  display:flex;
  flex:1 1
}

.app{
  display: flex;
  flex-direction: column;
}


